html,
body {
  height: 100%;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  margin: 0;
  font-weight: bold;
  font-family: 'Black Han Sans', sans-serif;
}
p,
a,
span {
  font-weight: normal;
  margin: 0;
  font-family: 'Gowun Dodum', serif;
}

ul,
ol,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  margin: 0;
  font-weight: normal;
  text-decoration: none;
}

.display-none {
  display: none;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  appearance: textfield;
  -moz-appearance: textfield;
}

/* section 공통 스타일 */
.section-header {
  margin: 30px 0;
}

.section-title {
  font-size: 48px;
}

.section-subtitle {
  font-size: 24px;
}

.section-content {
  margin: 0 auto;
  max-width: 75rem;
  padding: 0 1.5rem;
}

.pointer {
  cursor: pointer;
}

.typing-effect {
  animation: typing 2s steps(19) forwards, blink-caret 0.5s step-end infinite;
}

@media screen and (max-width: 1000px) {
  .section-header {
    margin: 30px 0 0;
  }
  .section-title {
    font-size: 40px;
  }
}

@media (max-width: 700px) {
  .section-title {
    font-size: 32px;
  }
  .section-subtitle {
    font-size: 16px;
  }
}

/* 타이핑 애니메이션 */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* 커서 깜빡임 애니메이션 */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #000;
  }
}
